                },
                {
                    id: 2,
                    title: "Albatross",
                    content:
                        "Vue‰πãÂêéÁöÑÁ¨¨‰∏Ä‰∏™ÁªÉÊâãÈ°πÁõÆÔºåÔºàÊé•Âè£Êù•Ëá™cnodeÔºâÔºå‰∏Ä‰∏™ NodeJS ËÆ∫ÂùõÈ°πÁõÆ„ÄÇ‰∏ëÈôãÁöÑUIÊù•Ëá™ÊàëÁ≤æÂøÉÁöÑËÆæËÆ°ÔºàÂ¶ÇÊûú‰Ω†ËßâÂæóËøò‰∏çÁÆó‰∏ëÔºåÂèØËÉΩÊòØ‰Ω†Ê≤°ÊúâËßÅËøáÁ¨¨‰∏ÄÁâàüê∑ÔºåÂ∞èÊ∏ÖÊñ∞ÁöÑÈÖçËâ≤ÊòØÊ∞∏ËøúÁöÑÁ•ûÔºâ„ÄÇ‰ΩøÁî® Vue Ê°ÜÊû∂‰ª•Âèä vue-router Âíå vuex Âà∂‰ΩúÔºåaxios ËØ∑Ê±ÇÊï∞ÊçÆÔºå ElementUI Â∫ì„ÄÇ",
