                    content: "Âú®È°πÁõÆÂºÄÂèë‰∏≠ÔºåÂÖ¨ÂÖ±Âá∫Êù•ÁöÑÂü∫‰∫é vue.js 2.x ÁöÑÊãñÂä®Êåá‰ª§ÔºåÂÖºÂÆπÁßªÂä®Á´Ø„ÄÇ‰ΩøÁî®ÊñπÊ≥ïÊàñ‰∏Ä‰∫õAPIËØ¶ËßÅGitHub Êàñ NPM„ÄÇ",
                    link: "https://www.npmjs.com/package/vue2-drag-directive",
                    imgSrc: "",
                    time: "2022.03",
                },
            ],
            flag: 1,
        };
